@media (orientation: portrait) {
  header.portrait {
    z-index: 1;
    opacity: 1;
    position: relative;
  }

  header.portrait.set-opacity {
    opacity: .5;
    animation: 4s fadeouthalf;
  }

  header.portrait h1 {
    text-align: right;
    padding: 0 40px;
  }

  main {
    width: 90vh;
    height: 100%;
    opacity: .5;
    position: absolute;
    top: 0;
    right: 60px;
    transform: rotate(90deg);
  }

  .about {
    height: 0;
    width: 0;
    background: #0000001a;
    position: absolute;
    bottom: 0;
    right: 0;
  }

  .about .about-content {
    background: #fff;
    border: 1px solid #0d0d0d;
    position: absolute;
    bottom: 20px;
    right: 20px;
  }

  .about.shrink, .about.shrink .about-content {
    height: 0;
    width: 0;
    padding: 0;
  }

  .about.shrink .about-content p, .about.shrink .about-content a {
    opacity: 0;
  }

  .about.shrink .about-content .circular svg {
    opacity: 0;
    transform: rotate(-70deg);
  }

  .about.grow {
    height: 100%;
    width: 100%;
    padding: 20px;
    animation: 1s grow;
  }

  .about.grow .about-content {
    height: calc(100% - 40px);
    width: calc(100% - 40px);
  }

  .about.grow .about-content p, .about.grow .about-content a {
    opacity: 1;
